$('.enterprises__menu').owlCarousel({
    loop: true,
    margin: 20,
    mouseDrag: false,
    touchDrag: true,
    dots: false,
    nav: true,
    navText: ['<i class="icon icon-left-open"></i>','<i class="icon icon-right-open"></i>'],
    items: 2,
    responsive: {
        0: {
            items: 2,
            margin: 10
        },
        576: {
            items: 3,
            margin: 15
        },
        640: {
            items: 4,
            margin: 15
        },
        768:{
            items: 5,
            margin: 15
        },
        1024:{
            items: 6
        },
        1200:{
            items: 8,
            margin: 10
        }
    }
});
if($(window).width() >=1024){
    $('.enterprises__list').slick({
        slidesToShow: 5,
        slidesToScroll: 1,
        dots: false,
        focusOnSelect: true,
        vertical: true,
        nextArrow: '<i class="icon icon-right-open next-btn"></i>',
        prevArrow: '<i class="icon icon-left-open prev-btn"></i>',
    });
}
$('.enterprises__list__item').on('click', function (){
  $('.enterprises__list__item').removeClass('active');
  $(this).addClass('active');
});
